/* modulo 42 - paginas do produto -  criando componente de produtos relacionados */
import React, { Component} from 'react';
import Produtos from '../../components/Listas/Produtos';

/* modulo 47 ( extra) integrando o componente de Produtos relacionados */

import { connect } from 'react-redux';
import  actions  from '../../redux/actions';

const PRODUTOS = [
	{
		id: 19239123,
		fotos: ['/static/img/mouse-1.png'],
		titulo: 'Mouser Gamer 1',
		preco: 25,
		promocao: 25,
	},

	{
		id: 37239137,
		fotos: ['/static/img/mouse-4.png'],
		titulo: 'Mouser Gamer 2 para ',
		preco: 35,
		promocao: 25,
	},

	{
		id: 26239129,
		fotos: ['/static/img/mouse-5.png'],
		titulo: 'Mouser Gamer 3',
		preco: 50,
		promocao: 40,
	},

	{
		id: 48239127,
		fotos: ['/static/img/mouse-2.png'],
		titulo: 'Mouser Gamer 4',
		preco: 135,
		promocao: 125,
	}
];

class ProdutosRelacionados extends Component {

	componentDidMount() {
	   
		if (this.props.produto) {
			
			this.props.fetchProdutosCategoria(this.props.produto.categoria._id, 0, 4);
		}
	}
	
	componentDidUpdate(prevProps) {
		 
		if (!prevProps.produto && this.props.produto) {

				this.props.fetchProdutosCategoria(this.props.produto.categoria._id, 0, 4);
		}
	 }

	render() {

		const { produtosCategoria } = this.props;

		return (
			<div className='container Produtos-Relacionados flex vertical'>
				<h2>Produtos Relacionados</h2>
				<br />

				<Produtos produtos={produtosCategoria ? produtosCategoria : []}
					itensPorLinha={4} />
			</div>
		);
	}
}


const mapStateToProps = state => ({

	produtoCategoria: state.categoria.produtosCategoria,
	produto : state.produto.produto
});


export default connect(mapStateToProps,actions)(ProdutosRelacionados);